import * as Actions from '../actionTypes';

const initialState = {
    SelectedCategory: null,
    SelectedPoint: null,
    blur: false
}

export default function (state = initialState,action: any) {
    switch (action.type) {

        case Actions.BLUR:
            return {
                ...state,
                blur: !state.blur
            }

        case Actions.SELECT_CATEGORY:
            const {cat_id} = action.payload;
            return {
                ...state,
                SelectedCategory: cat_id
            }

        case Actions.SELECT_POINT:
            const {point} = action.payload;
            return {
                ...state,
                SelectedPoint: point
            }

        default:
            return state;
    }
}