import React, { Component } from "react";
import styled from "styled-components";
import {connect} from 'react-redux';
import {getCategory, getGame} from '../redux/selector';
import {setWordChanged,increaseErrors,updatePoints,changeTurn,changeLap,finishGame,setErrorsToZero,setErrorJustHappened} from '../redux/actions';
import {useHistory} from 'react-router-dom';
import '../css/round.css';
import { bindActionCreators } from "redux";
import {IGame} from '../models/game';
import {ICategory} from '../models/category';
import getdb from '../database/getdb';
import * as Game from '../game/round_quick';
import star from '../assets/img/star.png';


interface IRoundPageState {
  word: any;
  time: number;
  started: boolean;
  finished: boolean;
  thinkingTime: number;
  myInterval: any;
}

const initialState: IRoundPageState = {
  word: [],
  time: 0,
  started: false,
  finished: false,
  thinkingTime: 0,
  myInterval: undefined
};

interface IRoundPageProps {
  setWordChanged: (state: boolean) => void;
  setErrorsToZero: () => void;
  setErrorJustHappened: (state: boolean) => void;
  increaseErrors: () => void;
  updatePoints: (point: number,time: number,turn: number) => void;
  changeTurn: (turn: number) => void;
  finishGame: () => void;
  changeLap: () => void;
  Setting: IGame;
  Category: ICategory;
  history: any;
}

class Round extends Component<IRoundPageProps,IRoundPageState> {

  constructor(props: IRoundPageProps) {
    super(props);
    this.state = initialState;
  }

  Container = styled.div`
    max-width: 40rem;
    padding: 7rem 0 1rem;
    width: 95vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  `;

  Buttons = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  `;

  TopButtons = styled.span`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin: 1rem 0;
  `;

  ControlButtons = styled.div`
    width: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    color: #fff;
    margin-top: 2rem;

    & p {
      font-size: .8rem;
      margin-top: .5rem
    }
  `;

  Word = styled.p`
    width: 100%;
    padding: 1rem 0;
    background-color: #fff;
    border-radius: 2rem;
    text-align: center;
    font-size: 1.3rem;
    color: #333;
  `;

  Button = styled.button`
    width: 4rem;
    height: 4rem;
    background-Color: #fff;
    border-Radius: 2rem;
    display: flex;
    align-Items: center;
    justify-Content: center;
    border: none;
    outline: none;
    color: #333;
    position: relative;

    & div {
      position: absolute;
      width: 1.5rem;
      height: 1.5rem;
      left: 0%;
      top: 0%;
      background-color: #E53935;
      border-radius: 50%;
      color: #fff;
      font-weight: bold;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  `;

  FinishingButtons = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  `;

  thinkingTimeBox = styled.div`
    width: 5rem;
    height: 5rem;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #333;
    border-radius: 50%;
    font-size: 2rem;
  `;

  Banner = styled.div`
  width: 100%;
  height: 3rem;
  background-color: #fff;
  border-radius: 2rem;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  font-size: 1.3rem;
  margin: 1.5rem 0 0 0;
  `;


  setWord = () => {
    let result = getdb(1,1);
    result.then((res: any) => {
      this.setState({
        word: res
      })
    });
  }

  componentDidMount() {
    this.setWord();
    this.setState({
      time: this.props.Setting.QuickModeGameTime,
    })
    this.props.setWordChanged(false);
    this.props.setErrorsToZero();
  }

  handleChangeWord = () => {   
    this.setWord();
    this.props.setWordChanged(true);
  }

  gameFinished = () => {
    clearInterval(this.state.myInterval);
    this.setState({
      finished: true
    })
  }

  handlePlay = () => {
    this.setState({
      started: true
    })
    const interval = setInterval(() => {
      if (this.state.time > 0) {
        this.setState(prev => {
          return {
            time: prev.time - 1
          }
        })
      }
      if (this.state.time === 0) {
        this.gameFinished();
        if(this.props.Setting.Turn < this.props.Setting.Groups.length) {
          this.props.changeTurn(this.props.Setting.Turn+1);
          this.setState({
            time: this.props.Setting.QuickModeGameTime,
            finished: false,
            started: false
          })
        }
        else {
          this.props.finishGame();
          this.props.history.push('./gameresult');
        }
      }
    }, 1000);
    this.setState({
      myInterval: interval
    })
  }

  handleFinished = () => {
    this.gameFinished();
  }

  right = () => {
    Game.handleRight(this.props);
    this.handleChangeWord();
  }

  render() {
    return (
      <this.Container>
        <this.Banner>
          <img src={star} alt='Star'/>
          <p>نوبت {this.props.Setting.Groups[this.props.Setting.Turn-1].name}</p>
          <img src={star} alt='Star'/>
        </this.Banner>
        <this.Word>{this.state.word}</this.Word>
        <this.Buttons>
          {!this.state.started ?
          <this.ControlButtons>
            <this.Button onClick={() => this.handlePlay()}>
              <i className="fa fa-play" style={{fontSize: '1.5rem'}}/>
            </this.Button>
            <p>شروع</p>
          </this.ControlButtons> : undefined}
          <this.TopButtons>
            <div className='timer' style={this.state.started && !this.state.finished ? {animationName: 'timer'} : undefined}>{this.state.time}</div>
          </this.TopButtons>
          {this.state.started ?
          <this.FinishingButtons>
          <a style={{textDecoration: 'none'}} onClick={() => this.right()}>
            <this.Button>
              <i className="fa fa-check" style={{color: '#388E3C',fontSize: '1.5rem'}}/>
            </this.Button>
          </a>
          <a style={{textDecoration: 'none'}} onClick={() => this.handleChangeWord()}>
            <this.Button>
              <i className="fa fa-times" style={{color: '#E53935',fontSize: '1.5rem'}}/>
            </this.Button>
          </a>
        </this.FinishingButtons> : null}
        </this.Buttons>
      </this.Container>
    );
  }
}

export default connect(store => ({Setting: getGame(store),Category: getCategory(store)}),(dispatch) => bindActionCreators({setWordChanged,increaseErrors,updatePoints,changeTurn,changeLap,finishGame,setErrorsToZero,setErrorJustHappened},dispatch))(Round);
