import React, { Component } from 'react';
import styled from 'styled-components';
import star from '../assets/img/star.png';
import Backpage from '../components/backpage';
import {Link} from 'react-router-dom';
import {addGroup,removeGroup,editGroup,addLap,removeLap,increaseThinkingTime,decreaseThinkingTime} from '../redux/actions';
import {connect} from 'react-redux';
import {getGame} from '../redux/selector';
import * as Actions from '../redux/actionTypes';
import createdb from '../database/createdb';
import { bindActionCreators } from "redux";
import {IGame} from '../models/game';

interface ISettingPageState {
    GroupNames: Array<string>,
}

const initialState: ISettingPageState = {
    GroupNames: [
        'گروه 1',
        'گروه 2',
        'گروه 3',
        'گروه 4'
    ],
};

interface ISettingPageProps {
    addGroup: (name: string) => void;
    removeGroup: () => void;
    addLap: () => void;
    removeLap: () => void;
    increaseThinkingTime: () => void;
    decreaseThinkingTime: () => void;
    editGroup: (id: number,value: string) => void;
    Game: IGame;
}

class Setting extends Component<ISettingPageProps,ISettingPageState> {

    constructor(props: ISettingPageProps) {
        super(props);
        this.state = initialState;
    }

    componentDidMount() {        
        createdb();
    }

    Container = styled.div`
        max-width: 40rem;
        width: 95vw;
        padding: 5rem 0 0;
        display: flex;
        flex-direction: column;
        align-items: center;
    `;

    Banner = styled.div`
        width: 100%;
        height: 3rem;
        background-color: #fff;
        border-radius: 2rem;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        font-size: 1.3rem;
        margin: 1.5rem 0 .5rem;
    `;

    Modes = styled.div`
        width: 100%;
        height: 12rem;
        background-color: #fff;
        border-radius: 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        font-size: 1.3rem;
    `;

    Counter = styled.div`
        width: 90%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        & button {
            width: 2.5rem;
            height: 2.5rem;
            background-color: #fff;
            border: none;
            border-radius: 2rem;

            &:focus {
                outline: none;
            }
        }

        & p {
            width: 3.5rem;
            height: 3.5rem;
            background-color: #fff;
            border-radius: 2rem;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    `;

    Input = styled.input`
        width: 90%;
        height: 2.5rem;
        padding: 0 1rem;
        background-color: #fff;
        border-radius: 2rem;
        border: none;
        margin: .5rem 0;
        font-family: inherit;

        &:focus {
            outline: none;
        }
    `;

    Startbtn = styled.button`
        padding: 0 1rem;
        height: 3rem;
        background-color: #fff;
        margin: 1rem 0;
        border: none;
        border-radius: 2rem;
        font-family: inherit;
        font-size: 1.2rem;
        
        & a {
            text-decoration: none;
            color: #333;
        }
    `;

    handleSetting = (action: any) => {
        switch (action) {
            case Actions.ADD_GROUP:
                if(this.props.Game.Groups.length < 4) {
                    this.props.addGroup('گروه');
                }
                break;

            case Actions.REMOVE_GROUP:
                if(this.props.Game.Groups.length > 2) {
                    this.props.removeGroup();
                }
                break;

            case Actions.ADD_LAP:
                if(this.props.Game.Laps < 20) {
                    this.props.addLap();
                }
                break;

            case Actions.REMOVE_LAP:
                if(this.props.Game.Laps > 3) {
                    this.props.removeLap();
                }
                break;

            case Actions.INCREASE_THINKING_TIME:
                if(this.props.Game.ThinkingTime < 60) {
                    this.props.increaseThinkingTime();
                }
                break;

            case Actions.DECREASE_THINKING_TIME:
                if(this.props.Game.ThinkingTime > 5) {
                    this.props.decreaseThinkingTime();
                }
                break;

            default:
                break;
        }
    }

    handleGroupNameChange = (e: any,index: number) => {

        let newGroupNames: Array<any> = [];

        for(let i = 0; i < this.state.GroupNames.length ; i++) {
            if(index !== i) {                
                newGroupNames.push(this.state.GroupNames[i]);
            }
            else {
                this.state.GroupNames[i] = e.target.value;
                newGroupNames.push(this.state.GroupNames[i]);
            }
        }        
    }

    handleGroupNameSave = () => {
        this.state.GroupNames.map((item ,index) => {
            this.props.editGroup(index,item);
        })
    }

    render() {
        return (
            <>
                <Backpage to='/modes'/>
                <this.Container>
                    <this.Banner>
                        <img src={star} alt='Star'/>
                        <p>تعداد گروه ها</p>
                        <img src={star} alt='Star'/>
                    </this.Banner>
                    <this.Counter>
                        <button onClick={() => this.handleSetting(Actions.ADD_GROUP)}>
                            <i className='fa fa-plus'></i>
                        </button>
                        <p>{this.props.Game.Groups.length}</p>
                        <button onClick={() => this.handleSetting(Actions.REMOVE_GROUP)}>
                            <i className='fa fa-minus'></i>
                        </button>
                    </this.Counter>
                    {this.props.Game.Groups.map((item: any,index: number) => {
                        return (
                            <this.Input key={index} defaultValue={item.name} onChange={(e) => this.handleGroupNameChange(e,index)}/>
                        )
                    })}
                    <this.Banner>
                        <img src={star} alt='Star'/>
                        <p>تعداد دورها</p>
                        <img src={star} alt='Star'/>
                    </this.Banner>
                    <this.Counter>
                        <button onClick={() => this.handleSetting(Actions.ADD_LAP)}>
                            <i className='fa fa-plus'></i>
                        </button>
                        <p>{this.props.Game.Laps}</p>
                        <button onClick={() => this.handleSetting(Actions.REMOVE_LAP)}>
                            <i className='fa fa-minus'></i>
                        </button>
                    </this.Counter>
                    <this.Banner>
                        <img src={star} alt='Star'/>
                        <p>زمان فکر کردن</p>
                        <img src={star} alt='Star'/>
                    </this.Banner>
                    <this.Counter>
                        <button onClick={() => this.handleSetting(Actions.INCREASE_THINKING_TIME)}>
                            <i className='fa fa-plus'></i>
                        </button>
                        <p>{this.props.Game.ThinkingTime}</p>
                        <button onClick={() => this.handleSetting(Actions.DECREASE_THINKING_TIME)}>
                            <i className='fa fa-minus'></i>
                        </button>
                    </this.Counter>
                    <this.Startbtn onClick={() => this.handleGroupNameSave()}>
                        <Link to='/gameresult'>مرحله بعد</Link>
                    </this.Startbtn>
                </this.Container>
            </>
        );
    }
}

export default connect(store => ({Game: getGame(store)}),(dispatch) => bindActionCreators({addGroup,removeGroup,editGroup,addLap,removeLap,increaseThinkingTime,decreaseThinkingTime},dispatch))(Setting);