import React, { Component } from "react";
import styled from "styled-components";
import {connect} from 'react-redux';
import {getCategory, getGame} from '../redux/selector';
import {setWordChanged,increaseErrors,updatePoints,changeTurn,changeLap,finishGame,setErrorsToZero,setErrorJustHappened} from '../redux/actions';
import {Link} from 'react-router-dom';
import '../css/round.css';
import { bindActionCreators } from "redux";
import {IGame} from '../models/game';
import {ICategory} from '../models/category';
import getdb from '../database/getdb';
import * as Game from '../game/round_normal';


interface IRoundPageState {
  word: any;
  time: number;
  started: boolean;
  finished: boolean;
  thinkingTime: number;
  myInterval: any;
}

const initialState: IRoundPageState = {
  word: [],
  time: 0,
  started: false,
  finished: false,
  thinkingTime: 0,
  myInterval: undefined
};

interface IRoundPageProps {
  setWordChanged: (state: boolean) => void;
  setErrorsToZero: () => void;
  setErrorJustHappened: (state: boolean) => void;
  increaseErrors: () => void;
  updatePoints: (point: number,time: number,turn: number) => void;
  changeTurn: (turn: number) => void;
  finishGame: () => void;
  changeLap: () => void;
  Setting: IGame;
  Category: ICategory;
  history: any;
}

class Round extends Component<IRoundPageProps,IRoundPageState> {

  constructor(props: IRoundPageProps) {
    super(props);
    this.state = initialState;
  }

  Container = styled.div`
    max-width: 40rem;
    padding: 7rem 0 1rem;
    width: 95vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  `;

  Buttons = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  `;

  TopButtons = styled.span`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin: 1rem 0;
  `;

  ControlButtons = styled.div`
    width: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    color: #fff;
    margin-top: 2rem;

    & p {
      font-size: .8rem;
      margin-top: .5rem
    }
  `;

  Word = styled.p`
    width: 100%;
    padding: 1rem 0;
    background-color: #fff;
    border-radius: 2rem;
    text-align: center;
    font-size: 1.3rem;
    color: #333;
  `;

  Button = styled.button`
    width: 4rem;
    height: 4rem;
    background-Color: #fff;
    border-Radius: 2rem;
    display: flex;
    align-Items: center;
    justify-Content: center;
    border: none;
    outline: none;
    color: #333;
    position: relative;

    & div {
      position: absolute;
      width: 1.5rem;
      height: 1.5rem;
      left: 0%;
      top: 0%;
      background-color: #E53935;
      border-radius: 50%;
      color: #fff;
      font-weight: bold;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  `;

  FinishingButtons = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  `;

  thinkingTimeBox = styled.div`
    width: 5rem;
    height: 5rem;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #333;
    border-radius: 50%;
    font-size: 2rem;
  `;

  setWord = () => {
    let result = getdb(Number(this.props.Category.SelectedCategory),Number(this.props.Category.SelectedPoint));
    result.then((res: any) => {
      this.setState({
        word: res
      })
    });
  }

  componentDidMount() {
    this.setWord();
    this.setState({
      time: this.props.Category.SelectedPoint*30 + 30,
      thinkingTime: this.props.Setting.ThinkingTime
    })
    this.props.setWordChanged(false);
    this.props.setErrorsToZero();
    let thinkingTime = setInterval(() => {
      if(this.state.thinkingTime > 0) {
        this.setState(prev => {
          return  {
            thinkingTime: prev.thinkingTime - 1
          }
        })
      }
      else {
        this.handlePlay();
        clearInterval(thinkingTime);
      }
    },1000)
  }

  handleChangeWord = () => {   
    this.setWord();
    this.props.setWordChanged(true);
  }

  gameFinished = () => {
    clearInterval(this.state.myInterval);
    this.setState({
      finished: true
    })
  }

  handlePlay = () => {
    this.setState({
      started: true
    })
    const interval = setInterval(() => {
      if (this.state.time > 0) {
        this.setState(prev => {
          return {
            time: prev.time - 1
          }
        })
      }
      if (this.state.time === 0) {
        this.gameFinished();
      }
    }, 1000);
    this.setState({
      myInterval: interval
    })
  }

  handleFinished = () => {
    this.gameFinished();
  }

  render() {
    return (
      <this.Container>
        <this.Word>{this.state.word}</this.Word>
        {!this.state.started ? <this.thinkingTimeBox>{this.state.thinkingTime}</this.thinkingTimeBox> : null}
        <this.Buttons>
          <this.TopButtons>
            {this.state.started ?
            <this.ControlButtons>
              <this.Button onClick={() => Game.handleError(this.props)}>
                <i className="fa fa-exclamation" style={{fontSize: '1.5rem'}}/>
                {this.props.Setting.Errors > 0 ? <div>{this.props.Setting.Errors}</div> : undefined}
              </this.Button>
              <p>خطا</p>
            </this.ControlButtons> :
            <this.ControlButtons>
             <this.Button disabled={this.props.Setting.isWordChanged ? true : false} style={this.props.Setting.isWordChanged ? {backgroundColor:'#ccc',color:'#666'} : undefined} onClick={() => this.handleChangeWord()}>
              <i className="fa fa-redo-alt" />
             </this.Button>
             <p>تعویض کلمه</p>
            </this.ControlButtons>
            }
            <div className='timer' style={this.state.started && !this.state.finished ? {animationName: 'timer'} : undefined}>{this.state.time}</div>
            <this.ControlButtons>
              <this.Button onClick={() => {this.state.started ? this.handleFinished() : this.handlePlay()}}>
                {this.state.started ? 
                <i className="fa fa-stop"/> : 
                <i className="fa fa-play"/>}
              </this.Button>
              <p>
                {this.state.started ? 'پایان' : 'شروع'}
              </p>
            </this.ControlButtons>
          </this.TopButtons>
          {this.state.finished ?
          <this.FinishingButtons>
          <Link to='/gameresult' style={{textDecoration: 'none'}} onClick={() => Game.handleRight(this.props,this.state)}>
            <this.Button>
              <i className="fa fa-check" style={{color: '#388E3C',fontSize: '1.5rem'}}/>
            </this.Button>
          </Link>
          <Link to='/gameresult' style={{textDecoration: 'none'}} onClick={() => Game.handleFalse(this.props)}>
            <this.Button>
              <i className="fa fa-times" style={{color: '#E53935',fontSize: '1.5rem'}}/>
            </this.Button>
          </Link>
        </this.FinishingButtons> : null}
        </this.Buttons>
      </this.Container>
    );
  }
}

export default connect(store => ({Setting: getGame(store),Category: getCategory(store)}),(dispatch) => bindActionCreators({setWordChanged,increaseErrors,updatePoints,changeTurn,changeLap,finishGame,setErrorsToZero,setErrorJustHappened},dispatch))(Round);
