import * as Actions from '../actionTypes';

const initialState = {
    Groups: [
        {
            name: 'گروه 1',
            score: 0
        },
        {
            name: 'گروه 2',
            score: 0
        }
    ],
    Laps: 3,
    ThinkingTime: 5,
    Turn: 1,
    isWordChanged: false,
    Errors: 0,
    ErrorJustHappened: false,
    RemainingTime: 0,
    GameFinished: false,
    CurrentLap: 1,
    QuickModeGameTime: 5
}

let groupNumber = 2;

export default function (state = initialState,action: any) {
    switch (action.type) {

        case Actions.ADD_GROUP:
            {
            groupNumber++;
            const {name} = action.payload;
            return {
                ...state,
                Groups: [
                    ...state.Groups,
                    {
                        name: name + ' ' + groupNumber,
                        score: 0
                    }
                ]
            }
        }

        case Actions.REMOVE_GROUP:
            {
            groupNumber--;
            let newGroups = [];
            const id = state.Groups.length-1;

            for(let i = 0;i < state.Groups.length; i++) {
                if(id !== i) {
                    newGroups.push(state.Groups[i]);
                }
            }

            return {
                ...state,
                Groups: newGroups
            }
        }

        case Actions.EDIT_GROUP:
            {
            const id = action.payload.id;
            const newValue = action.payload.newValue;

            let newGroups = [];

            for(let i = 0;i < state.Groups.length; i++) {
                if(id !== i) {
                    newGroups.push(state.Groups[i]);
                }
                else {
                    state.Groups[i] = {
                        name: newValue,
                        score: 0
                    }
                    newGroups.push(state.Groups[i]);
                }
            }

            return {
                ...state,
                Groups: newGroups
            }
        }

        case Actions.ADD_LAP:
            return {
                ...state,
                Laps: state.Laps + 1
            }

        case Actions.REMOVE_LAP:
            return {
                ...state,
                Laps: state.Laps - 1
            }

        case Actions.INCREASE_THINKING_TIME:
            return {
                ...state,
                ThinkingTime: state.ThinkingTime + 5
            }

        case Actions.DECREASE_THINKING_TIME:
            return {
                ...state,
                ThinkingTime: state.ThinkingTime - 5
            }

        case Actions.INCREASE_QUICKMODE_GAME_TIME:
            return {
                ...state,
                QuickModeGameTime: state.QuickModeGameTime + 10
            }

        case Actions.DECREASE_QUICKMODE_GAME_TIME:
            return {
                ...state,
                QuickModeGameTime: state.QuickModeGameTime - 10
            }

        case Actions.UPDATE_POINTS:
            const {score,remainingTime,index} = action.payload;
            let newGroups = [];

            for(let i = 0;i < state.Groups.length; i++) {
                if(index !== i) {
                    newGroups.push(state.Groups[i]);
                }
                else {
                    if(state.isWordChanged == true) {
                        state.Groups[i] = {
                            name: state.Groups[i].name,
                            score: state.Groups[i].score + Number(score) - (Math.floor(state.Errors / 2)) + Math.floor(Number(remainingTime) / 30) - 1
                        }
                    }
                    else {
                        state.Groups[i] = {
                            name: state.Groups[i].name,
                            score: state.Groups[i].score + Number(score) - (Math.floor(state.Errors / 2)) + Math.floor(Number(remainingTime) / 30)
                        }
                    }
                    if(state.QuickModeGameTime) {
                        state.Groups[i] = {
                            name: state.Groups[i].name,
                            score: state.Groups[i].score + Number(score)
                        }
                    }
                    
                    newGroups.push(state.Groups[i]);
                }
            }

            return {
                ...state,
                Groups: newGroups
            }

        case Actions.INCREASE_ERRORS:
            return {
                ...state,
                Errors: state.Errors + 1,
                ErrorJustHappened: true
            }

        case Actions.SET_ERRORS_TO_ZERO:
            return {
                ...state,
                Errors: 0
            }

            case Actions.SET_WORD_CHANGED:

            return {
                ...state,
                isWordChanged: action.payload.changed
            }

        case Actions.SET_ERROR_JUST_HAPPENED:
        return {
            ...state,
            ErrorJustHappened: action.payload.state
        }

        case Actions.CHANGE_TURN:
            const Turn = action.payload.turn
            return {
                ...state,
                Turn
            }

        case Actions.CHANGE_LAP:
        return {
            ...state,
            CurrentLap: state.CurrentLap + 1
        }

        case Actions.FINISH_GAME:
            return {
                ...state,
                GameFinished: !state.GameFinished
            }


        default:
            return state;
    }
}