import React, { Component } from 'react';
import Backpage from '../components/backpage';
import styled from 'styled-components';
import star from '../assets/img/star.png';
import categories from '../database/categories.json';
import {blur,selectCategory} from '../redux/actions';
import {connect} from 'react-redux';
import { bindActionCreators } from "redux";

interface ICategoryPageState {
}

const initialState: ICategoryPageState = {
};

interface ICategoryPageProps {
    blur: () => void;
    selectCategory: (id: number) => void;
}

class Category extends Component<ICategoryPageProps,ICategoryPageState> {

    Container = styled.div`
        max-width: 40rem;
        padding: 6rem .8rem 2rem;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-around;
        position: relative;
    `;

    Banner = styled.div`
    width: 100%;
    height: 3rem;
    background-color: #fff;
    border-radius: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    font-size: 1.3rem;
    margin: 1.5rem 0 .5rem;
    `;

    CategoryItem = styled.div`
        width: 48%;
        height: 8rem;
        background-color: #fff;
        border-radius: 2rem;
        margin: .2rem 0;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        color: #555;

        & i {
            font-size: 2.4rem;
        }

        & p {
            font-size: 1rem;
            margin-top: -1rem;
        }
    `;

    handleClick = (id: number) => {
        this.props.blur();
        this.props.selectCategory(id)
    }

    constructor(props: ICategoryPageProps) {
        super(props);
        this.state = initialState;
    }

    render() {
        return (
            <>
                <Backpage to='gameresult'/>
                <this.Container>
                    <this.Banner>
                        <img src={star} alt='Star'/>
                        <p>انتخاب دسته بندی</p>
                        <img src={star} alt='Star'/>
                    </this.Banner>
                    {categories.RECORDS.map((item,index) => {
                        return (
                            <this.CategoryItem key={index} onClick={() => this.handleClick(Number(item.ID))}>
                                <i className={item.Img}/>
                                <p>{item.CategoryName}</p>
                            </this.CategoryItem>
                        )
                    })}
                </this.Container>
            </>
        );
    }
}

export default connect(null,(dispatch) => bindActionCreators({blur,selectCategory},dispatch))(Category);
