export function handleError(this: any,props: any) {    
    const Errors: number = props.Setting.Errors;
    const ErrorLimit:number = 3;
    if(Errors < ErrorLimit) {
      props.setErrorJustHappened(true);
      setTimeout(() => {
        props.setErrorJustHappened(false);
      },1000)
      props.increaseErrors();

      if(Errors == ErrorLimit-1) {
        this.handleFalse(props);
        setTimeout(() => {
          props.history.push('/gameresult');
        },1000)
      }
    }
}

export function handleFalse (props: any) {
    if(props.Setting.Turn < props.Setting.Groups.length) {
      props.changeTurn(props.Setting.Turn+1)
    }
    else {
      if(props.Setting.CurrentLap == props.Setting.Laps) {
        props.finishGame();
      }
      else {
        props.changeLap();
      }
      props.changeTurn(1);
    }
}

export function handleRight (this: any,props: any,state: any) {
    props.updatePoints(props.Category.SelectedPoint,state.time,props.Setting.Turn-1);    
    if(props.Setting.Turn < props.Setting.Groups.length) {
      props.changeTurn(props.Setting.Turn+1)
    }
    else {
      if(props.Setting.CurrentLap == props.Setting.Laps) {
        props.finishGame();
      }
      else {
        props.changeLap();
      }
      props.changeTurn(1);
    }
  }
